import * as React from 'react';
import { MobileToolbarGroup } from '../types';
import fontLayout from './font';
import insertLayout from './insert';
import alignLayout from './align';

function getButton(focusIcon: string, icon: string) {
  return ({ Wrapper, panelShowing, ...rest }) => (
    <Wrapper
      type="big"
      {...rest}
      isActive={panelShowing}
      icon={panelShowing ? focusIcon : icon}
    />
  );
}

export const mobileLayout: MobileToolbarGroup[] = [
  {
    key: 'group1',
    items: [
      {
        name: 'insert',
        Component: getButton('plus', 'plus'),
        panel: { title: 'Insert', content: insertLayout },
      },
      {
        name: 'font',
        Component: getButton('font', 'font'),
        panel: { title: 'Fonts', content: fontLayout },
      },
      {
        name: 'align',
        Component: getButton('alignLeft', 'alignLeft'),
        panel: { title: 'Align', content: alignLayout },
      },
    ],
  },
  {
    key: 'group2',
    items: [
      'image.mobileBig',
      'list.mobileTask',
      'mention.mobile',
      'history.undoMobile',
      'history.redoMobile',
    ],
  },
  {
    key: 'fixed',
    fixedOnRight: true,
    items: ['keyboard'],
  },
];
